@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&family=Source+Code+Pro:wght@300&display=swap);
.header{width:100vw;height:10%;background-color:black;border:2px inset green}.header-half{width:50%;height:100%;display:flex;justify-content:space-around;min-width:300px;align-items:center}.input-bar{border-radius:10px;border:none;width:50%;height:100%;font-size:2vh;font-family:"Open Sans Condensed", sans-serif;font-family:"Source Code Pro", monospace;color:black;padding-left:1vw;background-color:#00ff77}.input-bar::-webkit-input-placeholder{color:black}.input-bar:-ms-input-placeholder{color:black}.input-bar::placeholder{color:black}.submit-button{border-radius:10px;border:none;width:20%;font-size:2vh;font-family:"Open Sans Condensed", sans-serif;font-family:"Source Code Pro", monospace;font-weight:bold;background-color:#007336;cursor:pointer}.submit-button:hover{background-color:white;box-shadow:0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #00ff77, 0 0 82px #00ff77, 0 0 92px #00ff77, 0 0 102px #00ff77, 0 0 151px #00ff77}.inventory-label{font-family:"Open Sans Condensed", sans-serif;font-family:"Source Code Pro", monospace;font-size:5vh;color:#fff;text-shadow:0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #00ff77, 0 0 82px #00ff77, 0 0 92px #00ff77, 0 0 102px #00ff77, 0 0 151px #00ff77}@media only screen and (max-width: 600px){.inventory-label{font-size:4vh}}.inventory-container{display:flex;justify-content:space-between;align-items:center;width:50%;height:100%;min-width:100px}.inventory-box{height:70%;width:15%;margin:2px;border:2px green outset;background-color:#00ff77}.arrows{display:flex;align-items:center;justify-content:space-between}

html,#root,.App{height:100%}body{height:100%;margin:0;overflow:hidden}@media only screen and (max-width: 1200px){body{overflow:visible}}

.background{background-size:50%;height:90%;width:100%;margin:0;min-width:1200px;min-height:600px;position:relative}.building{width:20%}#building1{height:50%;background-color:black}#bus{z-index:5;position:absolute;width:33%;bottom:21%;left:50%}.car{z-index:5;width:20%;height:20%;position:absolute}#car1{left:25%;bottom:20%}#car2{left:0%;bottom:17.5%}#traffic-light{position:absolute;left:90%;bottom:27.7%;width:10%;height:40%;display:flex;flex-direction:column;align-items:center;justify-content:center}#traffic-light .pole{background:#ffef00;background:linear-gradient(90deg, #ffef00 0%, #8b7400 100%);width:10%;height:70%}#traffic-light #lights{width:35%;height:30%;border-radius:10%;background:#393938;background:linear-gradient(90deg, #393938 0%, #000 100%);display:flex;flex-direction:column;justify-content:space-around;align-items:center}#traffic-light #lights .light{width:50%;height:30%;background-color:gray;border-radius:50%}.red-light-on{background-color:#fc3b3b !important;box-shadow:0 0 7px #ff0a01, 0 0 42px #ff0a01, 0 0 82px #ff0a01, 0 0 92px #ff0a01, 0 0 102px #ff0a01, 0 0 151px #ff0a01}.green-light-on{background-color:#86ff86 !important;box-shadow:0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #00ff77, 0 0 82px #00ff77, 0 0 92px #00ff77, 0 0 102px #00ff77, 0 0 151px #00ff77}#road{width:100%;height:20%;background:#636363;background:linear-gradient(180deg, #636363 0%, #000 100%);display:flex;align-items:center;justify-content:space-evenly;position:absolute;bottom:10%;-webkit-transform:rotateX(40deg);transform:rotateX(40deg)}.road-line{width:10%;height:7%;background-color:#ffff6b}#planter{width:100%;height:14%;position:absolute;bottom:0}#brick-border{width:100%;height:10%;background:repeating-linear-gradient(90deg, grey, grey 80px, #a5a5a5 80px, #a5a5a5 160px)}

